@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@100;200;300;400;500;600;800;900;1000&display=swap');
*,
*::after,
*::before{
    box-sizing : border-box;
    padding : 0;
    margin : 0;
}
html {
    font-size : 62.5%;
    scroll-behavior: smooth;
    font-family: 'Prompt', sans-serif;

}
@keyframes textShine {
    0% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 100% 50%;
    }
}

/*Main Component*/

.container{
    max-width : 150rem;
    width : 90%;
    margin : 0 auto;
    border-radius : 4rem;
    
}
.gridcontainer{
    display : grid;
    margin : 2.5rem 2.5rem 2.5rem 0;
    padding : 2.5rem 2.5rem 2.5rem 2.5rem;
    grid-template-columns:1fr 3fr;
    grid-template-rows:1fr 3fr;
    position : relative;
    height : 90vh;
    grid-gap : 1rem;
}

.main-background{
    padding-left : 3rem;
}

ul{
    list-style-type: none;
}

.grid-content{

    background-color : #e3f1fd;
    border-radius : 2rem;
    padding : 1rem;
}
.app-name{
    display : flex;
    justify-content : center;
    align-items : center; 
    box-shadow : 0.5rem 0.5rem 2rem grey;
    font-size : 3.5rem;
}
.crud-todo{
    padding : 2rem;
    display : flex;
    justify-content : space-between;

}
.crud-todo-text{
    font-size : 2rem;
    font-weight: 700;
    border-radius : 1rem;
    padding-left : 2rem;
    display : inline-block;
}
.qotd{
    display : flex;
    box-shadow : 0.5rem 0.5rem 2rem grey;
    flex-direction : column;
    justify-content : left;
    padding : 2rem;
}
.list-todo{
    box-shadow : 0.5rem 0.5rem 2rem grey;
    width : 80%;
    margin : 0 auto;
    max-height : 90vh;
    overflow-y :scroll;
}
.quote-heading{
    font-size : 3rem;
    font-weight : 700;
    padding-bottom : 0.5rem;
}
.quote-content{
    padding-top : 0.5rem;
    font-size : 2rem;
    font-weight : 600;
}
p {
    background: linear-gradient(
        to right,
        #7953cd 20%,
        #00affa 30%,
        #0190cd 70%,
        #764ada 80%
    );
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-size: 500% auto;
    animation: textShine 5s ease-in-out infinite alternate;
}
.todolist-vis{
    display : flex;
    flex-direction : row;
    justify-content : left;
    align-items : center;
}
.todo-operations{
    display : flex;
    flex-direction : row;
}
.todolist-content{
    display : flex;
    flex-direction : row;
    justify-content : space-between;
    align-items : center;
    box-shadow : 0.2rem 0.2rem grey;
    border-radius : 1rem;
    padding-left : 1.5rem;
}
.input-checkbox{
    height : 2rem;
    width : 2rem;
    background-color:antiquewhite;
    margin : 0.8rem;
    display : inline;
}
.todolist-message{
    font-size:1.5rem;
    font-weight : 600;
}
.oper{
    background-position: center;
    background-size:cover;
    background-repeat: no-repeat;
    height : 2.5rem;
    width : 2.5rem;
    margin : 0 1rem 0 0;
}
.add-operation{
    background-image : url('../images/add.png');
}
.oper:hover{
    cursor: pointer;
    transition : 0.3s;
    box-shadow : 0.2rem 0.2rem 1rem grey;  
    border-radius : 0.5rem; 
}
.delete-operation{
    background-image : url('../images/delete.png');
}
.edit-operation{
    background-image : url('../images/edit.png');
}
.input-text{
    height : 2.5rem;
    width : 18rem;
    padding : 0.8rem;
    background-color:rgb(255, 247, 236);
    border-radius : 1.5rem;
    margin-right : 1rem;
}
.crud-side-content{
    display : flex;
    flex-direction : column;
    justify-content:space-around;
    align-items : center;
}
.clock-image{
    background-image : url('../images/clock.png');
    background-position: center;
    background-size:cover;
    background-repeat: no-repeat;
    height : 15rem;
    width : 15rem;
}
.list-image{
    background-image : url('../images/list.png');
    background-position: center;
    background-size:cover;
    background-repeat: no-repeat;
    height : 10rem;
    width : 10rem;
}

.create-empty-component{
    display : flex;
    justify-content : space-between;
    align-items : center;
    flex-direction : column;
    padding-top : 5rem;
}

::-webkit-scrollbar {
    height: 1rem;
    width: 0.5rem;
    background-color:rgba(236,236,241,0);
}

.list-todo:hover::-webkit-scrollbar-thumb{
    background-color: rgb(87, 87, 126);
}

::-webkit-scrollbar-thumb {
    background-color: #e3f1fd;
    border-color: rgba(255,255,255,1);
    border-radius: 9999px;
    border-width: 1px;
}
::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 9999px;
}